/* Home.css */
body {
    margin: 0;
    font-family: 'Poppins', sans-serif; /* A more modern font */
    background: url('pic.jpg') no-repeat center center fixed; 
    background-size: cover;
    color: #fff;
}

.home-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    text-align: center;
    backdrop-filter: blur(5px); /* Gives a frosted glass effect */
}

.home-container h1 {
    font-size: 48px; /* Larger font size */
    font-weight: bold;
    margin-bottom: 0.5em;
}

.home-container p {
    font-size: 20px;
    width: 60%; /* More width for better alignment */
    line-height: 1.6;
}

.nav-links {
    margin-top: 30px;
    display: flex;
    justify-content: center;  /* Centers the buttons if they are not already */
    gap: 20px;  /* Adds space between buttons */
}


/* Home.css */
.nav-links a {
    text-decoration: none;
    color: #ffffff;  /* Bright white text */
    font-size: 18px;
    padding: 12px 24px;  /* Slightly larger padding for a better touch area */
    background-color: rgba(0, 123, 255, 0.8);  /* Semi-transparent blue */
    border: none;
    border-radius: 30px;  /* Rounded corners */
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.5);  /* Subtle shadow for depth */
    transition: all 0.3s ease;

    /* Adding a subtle gradient for depth */
    background-image: linear-gradient(to bottom right, #007bff, #0056b3);
}

.nav-links a:hover, .nav-links a:focus {
    background-color: #0056b3;  /* Darker blue on hover/focus */
    background-image: linear-gradient(to bottom right, #0062cc, #003a88);
    color: #fff;
    transform: scale(1.05);  /* Slightly enlarge on hover */
}
